.pizzaCard {
   width: 250px;
   margin-top: 40px;


   &-img {

      img {
         width: 250px;
         height: 250px;

      }
   }

   &__title {
      font-style: normal;
      font-weight: 800;
      font-size: 20px;
      line-height: 24px;
      text-align: center;
      letter-spacing: 0.01em;
      color: #000000;
      margin-top: 12px;
      min-height: 50px;
   }

   &-info {
      background: #F3F3F3;
      border-radius: 10px;
      margin-top: 10px;

      &-row {
         padding: 7px;
         display: flex;
         align-items: center;
         justify-content: space-between;

         &:nth-child(1) {
            span {
               min-width: 35%;
            }
         }
         &:nth-child(2) {
            span {
               min-width: 22%;
            }
         }
        span {
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
            text-align: center;
            letter-spacing: 0.015em;
            color: #2C2C2C;
            padding: 6px 12px;
            border-radius: 5px;
            cursor: pointer;
            
           
            
            &.active {
               box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
               background: white;

            }

            &.disabled {
               opacity: 0.4;
               pointer-events: none;
            }
        }
      }
   }

   &-footer {
      margin-top: 17px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      h2 {
         font-style: normal;
         font-weight: bold;
         font-size: 18px;
         line-height: 27px;
         letter-spacing: 0.015em;
         color: #000000;
      }

      button {
         background: #FE5F1E;
         border: 1px solid #FE5F1E;
         box-sizing: border-box;
         border-radius: 30px;
         min-width: 132px;
         font-style: normal;
         font-weight: bold;
         font-size: 16px;
         line-height: 19px;
         color: #FFFFFF;
         display: flex;
         align-items: center;
         justify-content: space-between;
         padding: 11px 18px;

         svg {
            width: 12px;
            height: 12px;
            fill: white;
         }

         &.active{
            background: transparent;
            color: #FE5F1E;

            span {
               font-style: normal;
               font-weight: bold;
               font-size: 13px;
               line-height: 16px;
               padding: 5px;
               width: 10px;
               height: 10px;
               margin-left: 5px;
               border-radius: 50%;
               color: white;
               background: #FE5F1E;
               display: flex;
               align-items: center;
               justify-content: center;
            }

            svg{
               fill: #FE5F1E;
            }
         }
      }
   }
}