body {
   margin: 0;
   padding: 0;
   font-family: sans-serif;
   background: #FFDF8C;

}

h1, h2, h3, h4, h5, h6, p {
   margin: 0;
   padding: 0;
}

.container {
   max-width: 1140px;
   width: 100%;
   margin: 0 auto;
   padding: 15px;

}

.app {
  background: #FFFFFF;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.03);
  border-radius: 10px;

  &-body {
      padding: 40px 25px;
      box-sizing: border-box;
  }
}

.header {
   border-bottom: 1px solid #F7F7F7;
}