.header {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 40px 25px;
   box-sizing: border-box;
   width: 100%;
  

   &-left {
      display: flex;
      align-items: center;

      &__img {
         width: 40px;
         height: 40px;
      }

      &-item {
         margin-left: 10px;

         &__title {
            font-style: normal;
            font-weight: 800;
            font-size: 24px;
            line-height: 29px;
            letter-spacing: 0.01em;
            text-transform: uppercase;
            color: #181818;
         }

         &__info {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;
            color: #7B7B7B;
         }
      }
   }

   &-right {
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-width: 150px;

       background: #FE5F1E;
       border-radius: 30px;
       padding: 12px 24px;
       box-sizing: border-box;
      &__totalPrice {
         font-style: normal;
         font-weight: bold;
         font-size: 16px;
         line-height: 19px;
         color: #FFFFFF;
      }

      &__basketBtn {
         cursor: pointer;
         display: flex;
         align-items: center;
         background: transparent;
         border: none;
         outline: none;
         font-style: normal;
         font-weight: bold;
         font-size: 16px;
         line-height: 19px;
         color: #FFFFFF;
         margin-left: 26px;
         position: relative;

         svg {
            margin-right: 5px;
         }

         &::before {
            content: "";
            position: absolute;
            width: 1px;
            height: 20px;
            background: rgba(255, 255, 255, 0.411);
            left: -13px;
         }
      }
   }
}