.body-sidebar {
   display: flex;
   align-items: center;
   justify-content: space-between;

   &-left {
      display: flex;
      align-items: center;

      span {
         margin-right: 10px;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        letter-spacing: 0.015em;
        color: #2C2C2C;
        background: #F9F9F9;
        border-radius: 30px;
        padding: 10px 20px;
        text-decoration: none;

        cursor: pointer;

        &.active {
            color: #FFFFFF;
            background: #282828;
        }
      }
   }

   &-right {
      display: flex;
      align-items: center;
      
      svg {
         margin-right: 7px;

      }

      span {
         margin-right: 8px;
         

      }

     .drop {
      position: relative;
       padding: 0 14px;
       width: 100px;

      h4 {
         font-style: normal;
         font-weight: normal;
         font-size: 14px;
         line-height: 17px;
         letter-spacing: 0.015em;
         color: #FE5F1E;
         text-decoration: 2px dashed orange;
         cursor: pointer;
         padding: 8px 0;
         animation: mymove 3s infinite;

         @keyframes mymove {
           25%{
              transform: translateY(-4px);
           }
          50% {
              transform: translateY(4px); 
           }
           75% {
              transform: translateY(-4px);
           }
           100%{
              transform: translateY(4px);
           }
         }

         
      }

      .sort {
         position: absolute;
         background: #FFFFFF;
         box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
         border-radius: 10px;
         left: 0;
         right: 0;
         height: 0;
         overflow: hidden;
         margin-top: 10px;

         transition: all ease 0.5s;
        
         box-sizing: border-box;

         &.show {
             height: 110px;
             transition: all ease 0.5s;
         }

      
         h5 {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.015em;
            color: #000000;
            padding: 10px 14px;
            cursor: pointer;

            &.active {
               color: #FE5F1E;
               background: rgba(255, 166, 0, 0.116);
            }

            &:hover {
              color: #FE5F1E;
               background: rgba(255, 166, 0, 0.116);
            }


         }
      }
     }
   }
}